const Stack = createNativeStackNavigator();

export default function App() {
  return (
    <NavigationContainer>
      <Stack.Navigator initialRouteName="Home">
        <Stack.Screen name="Home" component={HomeScreen} options={{ headerShown: false }}/>
        <Stack.Screen name="Game" component={PlayScreen} options={{ headerShown: false }} />
      </Stack.Navigator>
    </NavigationContainer>
  )
};



function HomeScreen({ navigation, route }) {
  const[connected, setConnected] = useState(false);
  const[log, setLog] = useState("");
  const[serve, setUrl] = useState("localhost:7777");
  let [fontsLoaded] = useFonts({
    NotoSans_400Regular,
  });
  let ConnectToSocket = () => {
    if(websocket != null && websocket.readyState == 1)
    {
      websocket.close();
      setConnected(false);
    }
    else
    {
    websocket = new WebSocket("ws://" + serve);
  }
  websocket.onopen = function (e) {
    console.log("Connected");
    setConnected(true);
    websocket.send("Hello world");
  };
  websocket.onmessage = function (e) {
    setLog(log + "\n" + e.data);
    console.log(e.data);
  };
  }
  let sendMessage = () => {
    websocket.send("Update");
  }
  return (
    <View style={styles.container}>
      <Text style={{width:700, height:100, textAlign:"center", marginBottom:50, color:'black', fontSize:65, fontFamily:'NotoSans_400Regular'}}>
          Crazy Game
        </Text>
        <TextInput style={{ height: 50, width: 300, backgroundColor: 'white', padding:10, fontSize:20 }} value={serve} onChangeText={text => setUrl(text)}/>
      <Pressable style={{ height: 50, width: 300, backgroundColor: 'red', marginTop:10}} onPress={() => ConnectToSocket()}>
        <Text style={{width:300, height:50, textAlign:"center", paddingTop:10, color:'white', fontSize:20}}>
          Connect to server
        </Text>
      </Pressable>

      <Text style={{width:300, height:50, textAlign:"center", paddingTop:10, color:'black', fontSize:20}}>
          {connected ? "Connected" : "Disconnected"}
        </Text>    

      <Pressable style={{ height: 50, width: 300, backgroundColor: 'green', marginTop:10 }} onPress={() => sendMessage()}>
        <Text style={{width:300, height:50, textAlign:"center", paddingTop:10, color:'white', fontSize:20}}>
          Send Message
        </Text>
      </Pressable>

      <Pressable style={{ height: 50, width: 300, backgroundColor: 'purple', marginTop:10}} onPress={() =>
        navigation.navigate('Game')
      }>
        <Text style={{width:300, height:50, textAlign:"center", paddingTop:10, color:'white', fontSize:20}}>
          Go to Game Page
        </Text>
      </Pressable>

      <Text style={{width:300, height:50, textAlign:"center", paddingTop:10, color:'black', fontSize:20}}>
          Log:{log}
        </Text>  
    </View>
  );
}
var websocket = null;


